.card {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin: 2px 8px 0 2px;
  border-radius: 40px;
  cursor: grab;
}

.dragged {
  opacity: 0.3;
}

.hovered {
  box-shadow: 0px 0px 2px 4px rgb(51, 51, 207);
  transform: scale(1.02);
}
